/*
**  define styling for element component with specified with classe  main header.
*/
.header {
	padding: map-get($space, l1);

	@include rwd-df(lg) {
		padding: map-get($space, s1);
	}

	position: absolute;
	// left: #{-1 * map-get($space, main)};
	top: map-get($space, main);
	left: map-get($space, main);
	right: map-get($space, main);
	z-index: 1000;

	&__content {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}

	&__logo {
		background-color: $color-white;
		@include boxShadow(s1);

		border-radius: 50%;
		display: block;
		text-decoration: none;
		@include squareSize(8rem);
		display: flex;
		justify-content: center;
		align-items: center;
		&__image {
			width: map-get($space, l3);
		}
	}

	.navbar {
		transform: scale(0);
		transform-origin: top right;
		opacity: 0;
		transition: all map-get($time, l3);

		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 1000;
		background-image: radial-gradient(#7ed56f, #28b485);

		display: flex;
		justify-content: center;
		align-items: center;

		&__list {
			@include absCenter;
			display: flex;
			flex-direction: column;
			align-items: center;
			gap: map-get($space, main);
			width: 100%;

			&__item {
				&__link {
					&:link,
					&:visited {
						font-size: map-get($font-size, l4);
						text-align: center;
						display: inline-block;
						@extend .u-heading-font;
						color: $color-white;
						padding: map-get($space, main) map-get($space, l1);
						border-radius: map-get($space, s4);

						background-image: linear-gradient(
							120deg,
							transparent 0%,
							transparent 50%,
							#fff 50%
						);
						background-size: 240%;
						overflow: hidden;
						transition: all 0.4s;
					}

					&:hover,
					&:active {
						background-position: 100%;
						color: map-get($color-primary, main);
						transform: translateX(1rem);
					}
				}
			}
		}
	}

	&__content.open .navbar {
		transform: scale(1);
		opacity: 1;
	}

	.toggler-btn {
		$bar-height: map-get($space, s3);
		$bar-transition: map-get($time, l1);
		$bar-color: map-get($color-grey, main);
		@include boxShadow(l2);

		position: fixed;
		top: map-get($space, l2);
		right: map-get($space, l2);
		z-index: 1001;

		background-color: $color-white;
		border-radius: 50%;

		padding: map-get($space, main);
		@include rwd-df(md) {
			display: block;
		}

		&__bars {
			display: block;
			position: relative;
			@include squareSize(map-get($space, l1));
			&__bar {
				position: absolute;

				display: block;
				width: 100%;
				height: $bar-height;
				background-color: $bar-color;
				#{$start}: 0;
				border-radius: map-get($space, s2);

				transition: top $bar-transition $bar-transition,
					opacity $bar-transition $bar-transition, transform $bar-transition;

				&:nth-child(1) {
					top: 0;
				}
				&:nth-child(2) {
					top: calc(50% - ($bar-height / 2));
				}
				&:nth-child(3) {
					top: calc(100% - $bar-height);
				}
			}
		}
	}

	&__content.open .toggler-btn {
		$bar-height: map-get($space, s3);
		$bar-transition: map-get($time, l1);
		$bar-color: map-get($color-grey, main);

		&__bars {
			&__bar {
				transition: top $bar-transition, opacity $bar-transition,
					transform $bar-transition $bar-transition;

				&:nth-child(1) {
					top: calc(50% - ($bar-height / 2));
					transform: rotate(45deg);
				}
				&:nth-child(2) {
					opacity: 0;
				}
				&:nth-child(3) {
					top: calc(50% - ($bar-height / 2));
					transform: rotate(-45deg);
				}
			}
		}
	}
}
