.link {
	&:link,
	&:visited {
		display: inline-block;
		font-size: map-get($font-size, s1);
		color: map-get($color-primary, main);
		font-family: map-get($font-weight, l2);
		text-decoration: none;
		border-bottom: map-get($font-size, s8) solid map-get($color-primary, main);
		padding: map-get($space, s2);
		transition: all map-get($time, main);
		border-radius: map-get($font-size, s5);
		@extend .u-heading-font;
		text-transform: lowercase;
	}

	&:hover,
	&:active {
		background-color: map-get($color-primary, main);
		color: $color-white;
		@include boxShadow(l4);

		transform: translateY(-2px);
	}
}
