.scroll-to-bottom-btn {
	&__container {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 10px;
		text-align: center;
		z-index: 4;
	}

	display: inline-block;
	border: 2px solid rgba(255, 255, 255, 0.7);
	color: #fff;
	width: 32px;
	height: 50px;
	border-radius: 24px;
	text-indent: -9999px;
	position: relative;
	cursor: pointer;

	&__dot {
		height: 8px;
		width: 8px;
		background: rgba(255, 255, 255, 0.9);
		border-radius: 10px;
		position: absolute;
		left: 10px;
		top: 6px;
		animation: mouse-animation 2.5s infinite linear;
	}
}

@keyframes mouse-animation {
	0% {
		opacity: 1;
		top: 6px;
	}

	35% {
		opacity: 0;
		top: 20px;
	}
	100% {
		opacity: 0;
		top: 6px;
	}
}
