.sec-testimonials {
	position: relative;
	z-index: 1;

	position: relative;
	&__overlay {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		z-index: -1;
		// opacity: 0.15;

		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			background: rgba(map-get($color-grey, main), 0.85);
			// background: rgba(47, 55, 60, 0.8);
		}

		&__video {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: map-get($space, l3);

		@include rwd-df(md) {
			grid-template-columns: 1fr;
			gap: map-get($space, l2);
		}
	}

	&__link {
		margin-top: map-get($space, l3);
		text-align: center;
	}
}
