.tour-card {
	position: relative;
	transform-style: preserve-3d;

	@include smallScreen-and-notHover {
		@include boxShadow(l4);
		border: map-get($space, s4) solid map-get($color-primary, l1);
		border-radius: map-get($space, s1);
		overflow: hidden;
	}

	&__front,
	&__back {
		backface-visibility: hidden;
		border-radius: map-get($space, s1);
		overflow: hidden;
		@include boxShadow(l4);

		border: map-get($space, s4) solid map-get($color-primary, l1);
		transition: transform 1.5s ease;

		@include smallScreen-and-notHover {
			@include boxShadow(none);
			border: none;
			transition: none;
			border-radius: 0;
		}
	}

	&__front {
		position: relative;
		z-index: 1;
		@include smallScreen-and-notHover {
			position: static;
		}
	}

	&__back {
		background-image: linear-gradient(
			to right bottom,
			map-get($color-primary, l1),
			map-get($color-primary, d1)
		);
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		transform: rotateY(-180deg);

		@include smallScreen-and-notHover {
			position: static;
			transform: none;
		}
	}

	&:hover &__front {
		transform: rotateY(180deg);
		@include smallScreen-and-notHover {
			transform: none;
		}
	}

	&:hover &__back {
		transform: rotateY(0);
		@include smallScreen-and-notHover {
			transform: none;
		}
	}

	&__img {
		margin-bottom: map-get($space, l1);
	}
	&__title {
		text-align: center;
		color: map-get($color-primary, d1);
	}
	&__features {
		display: flex;
		flex-direction: column;
		text-align: center;
		margin: 0 map-get($space, l2) map-get($space, l2);

		@include smallScreen-and-notHover {
			margin-bottom: map-get($space, s1);
		}

		&__item {
			padding: 1rem 0;
			&:not(:last-child) {
				border-bottom: map-get($space, s5) solid map-get($color-grey, l4);
				color: map-get($color-grey, l2);
			}
		}
	}

	&__back {
		&__content {
			@include absCenter;
			width: 75%;
			display: flex;
			flex-direction: column;
			align-items: center;
			gap: map-get($space, l6);

			@include smallScreen-and-notHover {
				padding: map-get($space, l2) 0;
				position: static;
				width: 100%;
				transform: none;
				gap: map-get($space, l1);
			}
		}
	}
	&__price {
		font-size: map-get($space, main);
		text-transform: uppercase;
		color: map-get($color-grey, main);
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: map-get($space, l2);
		&__value {
			font-size: map-get($font-size, l7);
			font-weight: map-get($font-weight, s2);
			@extend .u-heading-font;
		}
	}

	&__btn {
	}
}
