.sec-hero {
	height: calc(100vh - map-get($space, main));

	background-image: linear-gradient(
		to right,
		rgba(map-get($color-primary, l1), 0.9),
		rgba(map-get($color-primary, d1), 0.9)
	);

	color: $color-white;
	border-top-left-radius: map-get($font-size, main);
	border-top-right-radius: map-get($font-size, main);
	overflow: hidden;

	position: relative;

	&::before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		clip-path: polygon(0 0, 100% 0%, 100% 100%, 50% 90%, 0 100%);

		background-image: linear-gradient(
				to right bottom,
				rgba(map-get($color-primary, l1), 0.35),
				rgba(map-get($color-primary, d1), 0.35)
			),
			url(../../assets/img/hero/hero.jpg);
		@include backgroundImage;
		background-position: top;
	}

	&__content {
		@include absCenter;
		text-align: center;
	}

	&__title {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: map-get($space, main);
		&__main,
		&__sub {
			text-transform: uppercase;
			background-color: map-get($color-primary, main);
			border-radius: map-get($space, main);
		}

		&__main {
			display: block;
			font-size: map-get($font-size, l8);
			padding: map-get($space, s1);

			@extend .u-heading-font;
			@include rwd-df(xs) {
				font-size: map-get($font-size, l4);
				letter-spacing: 1.2rem;
				padding: map-get($space, s2);
			}
		}
		&__sub {
			font-size: map-get($font-size, l2);
			font-weight: map-get($font-weight, l3);
			letter-spacing: 1.1rem;
			line-height: map-get($line-height, main);
			padding: map-get($space, s2);
			padding-#{$start}: map-get($space, main);
			@include rwd-df(xs) {
				font-size: map-get($font-size, main);
				padding: map-get($space, s3);
				padding-#{$start}: map-get($space, s1);
			}
		}

		margin-bottom: map-get($space, l4);
		@include rwd-df(xs) {
			margin-bottom: map-get($space, l3);
		}
	}
}
