.feature {
	background-color: rgba($color-white, 0.8);
	font-size: map-get($font-size, main);
	padding: map-get($space, l1);
	text-align: center;
	border-radius: map-get($font-size, s3);
	@include boxShadow(l2);
	transition: transform map-get($time, main);

	&:hover {
		transform: translateY(-1.5rem) scale(1.03);
	}

	&__icon {
		@include squareSize(map-get($space, l5));
		margin-bottom: map-get($space, l1);
		color: map-get($color-primary, main);
	}
}
