.sec-cta {
	background-image: linear-gradient(
		to right bottom,
		rgba(map-get($color-primary, l1), 0.8),
		rgba(map-get($color-primary, d1), 0.8)
	);
	@include backgroundImage;
	// background-position: top;
	&__content {
		padding: 4rem;

		background-image: linear-gradient(
				105deg,
				rgba($color-white, 0.9) 0%,
				rgba($color-white, 0.9) 50%,
				transparent 50%
			),
			url('../../assets/img/cta/cta-bg.jpg');

		@include backgroundImage;
		border-radius: map-get($space, s3);
		@include boxShadow(l1);

		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: map-get($space, l2);

		@include rwd-df(md) {
			column-gap: 0;
			background-image: linear-gradient(
					105deg,
					rgba($color-white, 0.9) 0%,
					rgba($color-white, 0.9) 100%,
					transparent 50%
				),
				url('../../assets/img/cta/cta-bg.jpg');

			grid-template-columns: 1fr;
			width: 60%;
			margin: auto;
		}

		@include rwd-df(xs) {
			width: 90%;
		}
	}

	&__title {
		grid-column: span 2;
		margin: 0;
	}
}
