/*
**  Define font "haymaker" Locally
*/
@font-face {
	font-family: 'haymaker';
	src: url('../../assets/fonts/haymaker/haymaker.eot');
	src: url('../../assets/fonts/haymaker/haymaker.eot?#iefix')
			format('embedded-opentype'),
		url('../../assets/fonts/haymaker/haymaker.woff') format('woff'),
		url('../../assets/fonts/haymaker/haymaker.ttf') format('truetype'),
		url('../../assets/fonts/haymaker/haymaker.svg#haymakerregular')
			format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: swap;
}

