.sec-features {
	background-image: linear-gradient(
			to right bottom,
			rgba(map-get($color-primary, l1), 0.8),
			rgba(map-get($color-primary, d1), 0.8)
		),
		url(../../assets/img/features/features-bg.jpg);
	@include backgroundImage;

	&__content {
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		gap: map-get($space, l2);

		@include rwd-df(md) {
			grid-template-columns: repeat(2, 1fr);
			gap: map-get($space, l3);
		}

		@include rwd-df(xs) {
			grid-template-columns: 1fr;
			.feature {
				width: 85%;
				margin: 0 auto;
			}
		}
	}
}
