.sec-about {
	&__content {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: map-get($space, l4);

		@include rwd-df(lg) {
			grid-template-columns: 1fr;
		}
	}

	&__text {
	}

	&__images {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: 0;
		&__image {
			position: relative;
			transition: all map-get($time, main);

			&:nth-child(1) {
				top: 0%;
				#{$start}: 10%;
			}
			&:nth-child(2) {
				top: 20%;
				#{$start}: -10%;
				z-index: 1;
			}
			&:nth-child(3) {
				grid-column: span 2;
				justify-self: center;
				width: 50%;

				top: -40%;
				#{$start}: 0%;
				z-index: 2;
			}

			&:hover {
				border-radius: map-get($font-size, s2);
				outline: map-get($font-size, s4) solid map-get($color-primary, l1);
				outline-offset: map-get($font-size, s2);
				transform: scale(1.05) translateY(-0.5rem);
				@include boxShadow(l4);
				z-index: 20;
			}
		}

		&:hover &__image:not(:hover) {
			transform: scale(0.95);
		}
	}
}
