.btn {
	&,
	&:link,
	&:visited {
		display: inline-block;
		position: relative;
		font-size: map-get($font-size, l1);
		padding: map-get($space, s1) map-get($space, l1);
		border-radius: map-get($font-size, l11);
		font-weight: map-get($font-weight, l2);

		text-transform: uppercase;

		transition: all map-get($time, main);

		@include rwd-df(xs) {
			font-size: map-get($font-size, main);
			padding: map-get($space, s2) map-get($space, main);
		}

		&::before {
			content: '';
			display: inline-block;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			@include squareSize(100%);
			border-radius: map-get($font-size, l11);
			z-index: -1;
			transition: all map-get($time, main);
		}
	}

	&:hover,
	&:active {
		transform: translateY(-3px);
		@include boxShadow(main);
		&::before {
			background-color: transparent;
			transform: scaleX(1.4) scaleY(1.6);
			opacity: 0;
			visibility: hidden;
		}
	}

	&--outline {
		&,
		&:link,
		&:visited {
			background-color: $color-white;
			color: map-get($color-grey, l3);

			&::before {
				background-color: $color-white;
			}
		}
	}

	&--fill {
		&,
		&:link,
		&:visited {
			background-color: map-get($color-primary, main);
			color: $color-white;

			&::before {
				background-color: map-get($color-primary, main);
			}
		}
	}
}
