.modal {
	visibility: hidden;
	opacity: 0;
	transform: translateY(-100%);
	transform-origin: top center;
	transition: all map-get($time, l2) map-get($time, l2);

	&.show {
		transition: all map-get($time, l2);
		visibility: visible;
		opacity: 1;
		transform: translateY(-0);
	}

	&.show &__content {
		transition: all map-get($time, l2) map-get($time, l2);
		transform: translate(-50%, -50%) scale(1);
		max-height: 85vh;
		overflow-y: auto;
	}

	backdrop-filter: blur(10px);
	background-color: rgba($color-black, 0.3);

	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 2000;
	&__content {
		transition: transform map-get($time, l2);
		padding: map-get($space, l2);
		@include absCenter;
		transform: translate(-50%, -50%) scale(0);
		background-color: $color-white;
		@include boxShadow(l4);

		border-radius: 3px;

		width: 60%;
		@include rwd-df(md) {
			width: 80%;
		}

		@include rwd-df(xs) {
			width: 90%;
		}
	}
	&__title {
		margin-bottom: map-get($space, l1);
	}

	&__text {
		font-size: map-get($font-size, s1);
		margin-bottom: map-get($space, l2);
		column-count: 2;
		column-gap: map-get($space, l2);
		column-rule: map-get($space, s5) solid #eee;
		hyphens: auto;

		@include rwd-df(xs) {
			column-count: 1;
		}
	}

	&__close-btn {
		display: block;
		position: absolute;
		top: map-get($space, s2);
		right: map-get($space, main);
		@extend .u-heading-font;
		color: map-get($color-grey, main);
		font-size: map-get($font-size, l6);
		transition: all map-get($time, main);

		&:hover {
			transform: scale(1.2);
		}
	}
}
