/*
**  define styling for element components with specified with classe main footer.
*/
.footer {
	padding: map-get($space, l5) 0;
	background-color: map-get($color-grey, d1);
	border-bottom-left-radius: map-get($font-size, main);
	border-bottom-right-radius: map-get($font-size, main);

	&__content {
		display: flex;
		justify-content: space-between;

		@include rwd-df(md) {
			flex-direction: column;
			gap: map-get($space, l2);
			align-items: center;
		}
	}

	&__logo-link {
		&__image {
			width: map-get($space, l7);
		}
	}

	&__info {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		@include rwd-df(md) {
			gap: map-get($space, l2);
		}
		&__nav {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			gap: map-get($space, l1);

			&__link {
				&:link,
				&:visited {
					@extend .u-heading-font;
					display: inline-block;
					color: map-get($color-grey, l5);
					background-color: map-get($color-grey, d1);
					text-decoration: underline;
					text-transform: uppercase;
					transition: all map-get($time, main);
				}
				&:hover,
				&:active {
					text-decoration: none;
					color: map-get($color-primary, main);
					@include boxShadow(l4);
					transform: rotate(5deg) scale(1.3);
				}
			}
		}

		&__copyright {
			text-align: center;
			font-size: map-get($font-size, main);
			color: map-get($color-grey, l5);
		}
	}
}
