.testimonial {
	background-color: rgba(#fff, 0.9);
	$skewdegre: 5deg;
	display: grid;
	grid-template-columns: 1fr 3fr;
	gap: map-get($space, l1);

	@include rwd-df(xl) {
		grid-template-columns: 1fr 2fr;
	}

	@include rwd-df(xs) {
		grid-template-columns: 1fr;
		gap: map-get($space, l2);
	}

	align-items: center;
	padding: map-get($space, main);
	border: map-get($space, s4) solid map-get($color-primary, main);
	border-radius: map-get($font-size, s7);
	transition: all map-get($time, l3);

	&__figure {
		position: relative;
		overflow: hidden;
		transition: all map-get($time, l3);
		@include rwd-df(xs) {
			width: 50%;
			margin: 0 auto;
		}

		&__image {
			border-radius: 50%;
			border: map-get($font-size, s7) solid map-get($color-primary, main);
		}
		&__name {
			@extend .u-heading-font;
			@include absCenter;
			top: 150%;
			text-align: center;
			width: 100%;
			color: map-get($color-primary, l1);
			font-size: map-get($font-size, l1);
			background-color: rgba(map-get($color-grey, l1), 0.8);
			padding: map-get($space, s1) map-get($space, s4);
			border-radius: map-get($font-size, s3);
			transition: all map-get($time, l3);
		}
	}
	&:hover {
		transform: skewX(-1 * $skewdegre);
		@include boxShadow(l3);
	}

	&:hover {
		transform: skewX(-1 * $skewdegre);
	}

	&:hover &__figure {
		transform: skewX(1 * $skewdegre);
		&__image {
		}

		&__name {
			top: 50%;
		}
	}

	&__quote {
		// background-color: red;
		transition: all map-get($time, l3);
		@include rwd-df(xs) {
			text-align: center;
		}
		&__title {
		}
		&__text {
		}
	}

	&:hover &__quote {
		transform: skewX(#{$skewdegre});
	}
}
