.form {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	@include rwd-df(md) {
	}
	gap: map-get($space, l2);

	@include rwd-df(xs) {
		gap: map-get($space, main);
	}

	.input-control {
		grid-column: span 2;
		&__input {
			font-size: map-get($font-size, l2);
			padding: map-get($space, main) map-get($space, main);
			border-radius: map-get($space, s4);
			background-color: rbga($color-white, 0.5);
			border: none;
			border-bottom: map-get($space, s3) solid transparent;
			display: block;
			width: 90%;
			transition: all map-get($time, main);

			@include rwd-df(md) {
				width: 100%;
			}

			&:focus {
				outline: none;
				@include boxShadow(l3);
				border-bottom-color: map-get($color-primary, main);

				&:invalid {
					border-bottom-color: #ff7730;
				}
			}

			&::-webkit-input-placeholder {
				color: map-get($color-grey, l3);
			}

			&:placeholder-shown + .input-control__label {
				opacity: 0;
				visibility: hidden;
				transform: translateY(-4.4rem);
			}
		}

		&__label {
			font-size: map-get($font-size, s1);
			font-weight: map-get($font-weight, l3);
			margin-left: map-get($space, main);
			margin-top: map-get($space, s2);
			display: block;
			transition: all map-get($time, l1);
			transform: translateY(0px);
		}
	}

	.radio-control {
		display: flex;
		align-items: center;
		gap: map-get($space, s1);
		@include rwd-df(xs) {
			grid-column: span 2;
		}

		&__input {
			display: none;
		}

		&__btn {
			display: block;
			@include squareSize(map-get($space, l1));
			border: map-get($space, s3) solid map-get($color-primary, main);
			border-radius: 50%;
			position: relative;

			&::after {
				content: '';
				display: block;
				@include squareSize(map-get($space, s2));
				border-radius: 50%;
				background-color: map-get($color-primary, main);
				@include absCenter;

				opacity: 0;
				transform: translate(-50, -50%) scale(0);
				transition: all map-get($time, l2);
			}
		}

		&__input:checked + .radio-control__btn::after {
			opacity: 1;
			transform: translate(-50, -50%) scale(1);
		}

		&__label {
			font-size: map-get($font-size, l2);

			cursor: pointer;
		}
	}

	&__btn {
		justify-self: start;
		@include rwd-df(sm) {
			grid-column: span 2;
		}
		&:link,
		&:visited {
			font-size: map-get($font-size, main);
		}
	}
}
