.sec__title {
	@extend .u-heading-font;
	width: fit-content;
	text-align: center;
	margin: 0 auto;
	text-transform: uppercase;

	transition: all map-get($time, main);

	margin-bottom: map-get($space, l4);
	font-size: map-get($font-size, l4);

	@include rwd-df(sm) {
		line-height: 3rem;
		font-size: map-get($font-size, l3);
		margin-bottom: map-get($space, l2);
	}

	font-weight: map-get($font-weight, l3);

	background-image: linear-gradient(to right, #7ed56f, #28b485);
	-webkit-background-clip: text;
	background-clip: text;
	color: transparent;

	&--white {
		background-image: none;
		-webkit-background-clip: text;
		background-clip: text;
		color: $color-white;
	}

	&:hover {
		transform: skewY(2deg) skewX(15deg) scale(1.1);
		text-shadow: 0.5rem 1rem 2rem rgb(0 0 0 / 20%);
	}
}
