.sec-tours {
	&__content {
		display: grid;
		gap: map-get($space, l7);
		grid-template-columns: repeat(3, 1fr);

		@include rwd-df(xl) {
			gap: map-get($space, l3);
		}

		@include rwd-df(lg) {
			grid-template-columns: repeat(2, 1fr);
			gap: map-get($space, l4);
		}

		@include rwd-df(xs) {
			grid-template-columns: 1fr;
			gap: map-get($space, l4);
		}

		perspective: 10000rem;
	}

	&__link {
		margin-top: map-get($space, l3);
		text-align: center;
	}
}
