.scroll-to-top-btn {
	position: fixed;
	bottom: map-get($space, l1);
	#{$end}: map-get($space, l1);
	z-index: 1001;
	padding: map-get($space, s1);
	background-color: map-get($color-grey, l4);
	color: map-get($color-primary, l5);
	@include squareSize(map-get($space, l3));
	font-size: map-get($font-size, l2);

	font-weight: map-get($font-weight, l3);
	line-height: 0;
	display: flex;
	justify-content: center;
	align-items: center;

	display: block;
	visibility: hidden;
	opacity: 0;
	pointer-events: none;
	transform: scale(0);
	transition: all map-get($time, l1);
	border: map-get($space, s4) solid map-get($color-primary, main);
	@include boxShadow(l3);

	&.show {
		visibility: visible;
		opacity: 1;
		transform: scale(1);
		pointer-events: auto;

		&:hover {
			background-color: map-get($color-primary, main);
			color: $color-white;
			border-color: transparent;
			border-radius: map-get($font-size, s1);
		}
	}
}
